<template>
	<div class="menubar_container">
		<div class="inner_frame" :fixed="forceFix || mustFix">
			<div class="menu_group fixed_frame left">
				<img src="/pic/logo.jpg" @click="onPromote">
				<div class="caption" @click="onPromote">海德税务师<br>事务所</div>
			</div>
			<div class="menu_group float_frame">
				<a class="menu_item" @click="goServices">业务咨询</a>
				<a class="menu_item">服务项目</a>
				<span class="center">
					<a class="menu_item"><i class="fa fas fa-search"></i></a>
				</span>
				<a class="menu_item">最新消息</a>
				<a class="menu_item">法务动态</a>
			</div>
			<div class="menu_group fixed_frame right"><a class="menu_item" :href="'mailto:'+email">联系我们</a></div>
		</div>
	</div>
</template>

<style scoped>
.menubar_container {
	position: relative;
	z-index: 10;
	height: 60px;
}
.inner_frame {
	height: 60px;
	padding-top: 20px;
	padding-bottom: 20px;
	border-top: 1px solid rgb(175, 175, 175);
	border-bottom: 1px solid rgb(175, 175, 175);
	background-color: white;
	line-height: 20px;
	transition: all 300ms ease-in-out;
}
.float_frame {
	height: 0px;
	text-align: center;
}
.fixed_frame {
	position: fixed;
	top: 0px;
	transition: all 300ms ease-in-out;
}
.fixed_frame.left {
	left: 0px;
}
.fixed_frame.right {
	right: 10px;
	padding-top: 10px;
	padding-bottom: 10px;
}
.fixed_frame.left img {
	position: relative;
	top: 10px;
	left: 10px;
	width: 80px;
	height: 80px;
	border-radius: 40px;
	z-index: 1;
	transition: all 300ms ease-in-out;
	cursor: pointer;
}
.fixed_frame.left div.caption {
	position: absolute;
	top: 10px;
	left: 0px;
	width: 100px;
	opacity: 0;
	font-weight: bolder;
	text-align: center;
	transition: all 300ms ease-in-out;
}
.menu_group a.menu_item {
	margin-left: 20px;
	margin-right: 20px;
	font-weight: bolder;
	cursor: pointer;
}
.float_frame span.center {
	box-sizing: border-box;
	display: inline-block;
	position: relative;
	top: -20px;
	width: 80px;
	height: 59px;
	background-color: rgb(205, 205, 205);
	cursor: pointer;
}
.float_frame span.center a.menu_item {
	position: relative;
	top: 20px;
}
.fixed_frame a.menu_item {
	color: rgb(225, 225, 225);
	text-shadow: 0px 0px 2px rgb(36, 36, 36);
	transition: all 300ms ease-in-out;
}
.inner_frame[fixed="true"] {
	position: fixed;
	top: 0px;
	left: 0px;
	width: 100%;
	box-shadow: 0px 3px 5px rgba(36, 36, 36, 0.5);
}
.inner_frame[fixed="true"] .fixed_frame.left img {
	top: 1px;
	left: 0px;
	width: 58px;
	height: 58px;
	border-radius: 0px;
}
.inner_frame[fixed="true"] .fixed_frame.left div.caption {
	left: 65px;
	opacity: 1;
}
.inner_frame[fixed="true"] .fixed_frame.right {
	padding-top: 20px;
	padding-bottom: 20px;
}
.inner_frame[fixed="true"] .fixed_frame a.menu_item {
	color: rgb(26, 26, 26);
	text-shadow: 0px 0px 0px transparent;
}
</style>

<script>
var current;
EventMetro.sub('home_scroll', (top, width, over) => {
	if (!current) return;
	current.onScroll(over);
});

export default {
	name: "HomeMenu",
	data () {
		return {
			forceFix: false
		}
	},
	props: {
		email: '',
		mustFix: false,
	},
	mounted () {
		current = this;
	},
	unmounted () {
		current = null;
	},
	methods: {
		onScroll (over) {
			this.forceFix = over;
		},
		onPromote () {
			EventMetro.pub('promote');
		},
		goServices () {
			if (this.$route.name === 'Services' || this.$route.name === 'Home') {
				let hash = this.$route.query.hash;
				if (!!hash) {
					this.$router.push({path: '/services', query: {hash}});
				}
				else {
					this.$router.push({path: '/services'});
				}
			}
			else {
				this.$router.push({path: '/services'});
			}
		},
	}
}
</script>