<template>
	<div class="workerwall_container">
		<div v-if="workers.length===0" class="info">暂无信息……</div>
		<div v-for="(worker, i) in workers" class="worker_item" :style="{left:(i*210+10-offset)+'px'}" @click="showWorker(worker.id)">
			<div class="pic" :style="{backgroundImage:'url('+worker.pic+')'}"></div>
			<div class="title">{{worker.title}}</div>
			<div class="name">{{worker.name}}</div>
		</div>
		<div class="button left" :invalid="offset<=0" @mouseenter="moveIn(true)" @mouseleave="moveOut(true)" @click="moveLeft()">
			<i class="fa fas fa-angle-left"></i>
		</div>
		<div class="button right" :invalid="offset>=max" @mouseenter="moveIn(false)" @mouseleave="moveOut(false)" @click="moveRight()">
			<i class="fa fas fa-angle-right"></i>
		</div>
	</div>
</template>

<style scoped>
.workerwall_container {
	position: relative;
	height: 360px;
	overflow: hidden;
}
.workerwall_container > .info {
	text-align: center;
	font-size: 20px;
	font-weight: bold;
}
.workerwall_container > .worker_item {
	position: absolute;
	top: 15px;
	z-index: 0;
	width: 200px;
	height: 330px;
	background-color: rgb(245, 245, 245);
	box-shadow: 1px 1px 3px rgba(75, 75, 75, 0.3);
	cursor: pointer;
	transition: all 300ms ease-in-out;
}
.workerwall_container > .worker_item:hover {
	z-index: 1;
	box-shadow: 3px 3px 8px rgba(36, 36, 36, 0.5);
	transform: scale(1.1);
}
.workerwall_container > .worker_item > .pic {
	width: 200px;
	height: 220px;
	background-size: contain;
	background-position: center;
	background-repeat: no-repeat;
	background-color: rgb(205, 205, 205);
	border-bottom: 1px solid rgb(205, 205, 205);
}
.workerwall_container > .worker_item > .title {
	margin-top: 30px;
	font-size: 16px;
	text-align: center;
}
.workerwall_container > .worker_item > .name {
	margin-top: 10px;
	font-size: 20px;
	font-weight: bolder;
	text-align: center;
}
.workerwall_container > .button {
	position: absolute;
	width: 50px;
	height: 100%;
	z-index: 5;
	background-color: rgba(75, 75, 75, 0.5);
	opacity: 0.3;
	cursor: pointer;
	transition: all 300ms ease-in-out;
}
.workerwall_container > .button:hover {
	background-color: rgba(36, 36, 36, 0.5);
	opacity: 1;
}
.workerwall_container > .button[invalid="true"] {
	opacity: 0;
	pointer-events: none;
}
.workerwall_container > .button.left {
	left: 0px;
}
.workerwall_container > .button.right {
	right: 0px;
}
.workerwall_container > .button > i {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	font-size: 60px;
	color: white;
	text-shadow: 0px 0px 2px black;
}
@media screen and (max-width: 600px) {
}
</style>

<script>
const ScrollDelta = 10, ScrollDelay = 100;
var current, timer;
EventMetro.sub('update_worker_wall', (slides) => {
	if (!current) return;
	current.init(slides);
});
EventMetro.sub('window_resize', (width, height) => {
	if (!current) return;
	current.onResize(width, height);
});

export default {
	name: "HomeWorkerWall",
	data () {
		return {
			workers: [],
			offset: 0,
			max: 0,
			width: 0,
			height: 0,
		}
	},
	mounted () {
		current = this;
		var rect = document.body.getBoundingClientRect();
		this.onResize(rect.width, rect.height);
	},
	unmounted () {
		current = null;
		if (!!timer) {
			clearTimeout(timer);
			timer = null;
		}
	},
	methods: {
		init (workers) {
			this.offset = 0;
			workers.forEach(item => {
				this.workers.push({
					id: item.id,
					name: item.name,
					title: item.title,
					pic: item.pic || '/pic/nobody.webp',
				});
			});
			var width = Math.min(this.width, 780);
			var max = 10 + this.workers.length * 210;
			if (width >= max) {
				this.offset = (max - width) / 2;
				this.max = this.offset;
			}
			else {
				max = max - width;
				this.max = max;
			}
		},
		onResize (w, h) {
			this.width = w;
			this.height = h;
			var width = Math.min(this.width, 780);
			var max = 10 + this.workers.length * 210;
			if (width >= max) {
				this.offset = (max - width) / 2;
				this.max = this.offset;
			}
			else {
				max = max - width;
				this.max = max;
			}
		},
		moveIn (isLeft) {
			if (!!timer) clearTimeout(timer);
			if (isLeft) timer = setTimeout(this.scrollLeft, ScrollDelay);
			else timer = setTimeout(this.scrollRight, ScrollDelay);
		},
		moveOut (isLeft) {
			if (!!timer) {
				clearTimeout(timer);
				timer = null;
			}
		},
		moveLeft () {
			if (!!timer) {
				clearTimeout(timer);
				timer = null;
			}
			var index = Math.floor((this.offset - 10) / 210);
			index = Math.max(0, index - 1);
			this.offset = index * 210 + 10;
		},
		moveRight () {
			if (!!timer) {
				clearTimeout(timer);
				timer = null;
			}
			var index = Math.floor((this.offset - 10) / 210);
			index ++;
			this.offset = Math.min(this.max, index * 210 + 10);
		},
		scrollLeft () {
			if (!!timer) {
				clearTimeout(timer);
				timer = null;
			}
			if (this.offset <= 0) {
				this.offset = 0;
				return;
			}
			var offset = this.offset - ScrollDelta;
			if (offset < 0) offset = 0;
			this.offset = offset;
			timer = setTimeout(this.scrollLeft, ScrollDelay);
		},
		scrollRight () {
			if (!!timer) {
				clearTimeout(timer);
				timer = null;
			}
			if (this.offset >= this.max) {
				this.offset = this.max;
				return;
			}
			var offset = this.offset + ScrollDelta;
			if (offset > this.max) offset = this.max;
			this.offset = offset;
			timer = setTimeout(this.scrollRight, ScrollDelay);
		},
		showWorker (wid) {
			console.log('/worker/' + wid);
			// this.$router.push({path: '/worker/' + wid});
		},
	}
}
</script>