<template>
	<div ref="container" class="container">
		<Slider :delay="slideDelay" />
		<HomeMenu :email="companyEmail" />
		<div class="content short company-desc">
			<div class="title">事务所简介</div>
			<div ref="companyDesc" class="context"></div>
		</div>
		<div class="content map">
			<div class="legend">
				<div class="title">联系我们</div>
				<div class="name">{{companyName}}</div>
				<div class="position">{{companyPos}}</div>
				<div class="email"><a :href="'mailto:'+companyEmail">{{companyEmail}}</a></div>
				<div class="tel">Tel: {{companyTel}}</div>
			</div>
			<div ref="baidumap" class="map"></div>
		</div>
		<div class="content short services-list">
			<div class="title">业务范围</div>
			<div v-for="itemList in services" class="serviceCategory">
				<div class="subtitle">{{itemList.name}}</div>
				<div v-for="item in itemList.list" class="listitem" @click="goService(item.id)">{{item.name}}</div>
			</div>
		</div>
		<div class="content short">
			<div class="title">专业团队</div>
			<HomeWorkerWall />
		</div>
		<div class="content short">
			<div class="title">业界新闻</div>
			<div v-for="item in news" class="news_item" @click="goNews(item.type, item.id)">
				<div class="title">{{item.title}}</div>
				<div class="desc">{{item.desc}}</div>
				<div class="stamp">{{item.stamp}}</div>
			</div>
		</div>
		<FootMark />
	</div>
	<QRCode />
</template>

<style scoped>
.container {
	position: fixed;
	top: 0px;
	left: 0px;
	width: 100%;
	height: 100%;
	margin-left: 0px;
	margin-right: 0px;
	overflow: auto;
	background-color: white;
}
.content {
	position: relative;
	margin-top: 50px;
	margin-bottom: 20px;
	padding: 20px 10px 10px 10px;
	border-top: 5px double rgb(225, 225, 225);
	border-bottom: 1px dashed rgb(205, 205, 205);
}
.content.short {
	width: 800px;
	max-width: 100%;
	margin-left: auto;
	margin-right: auto;
}
.content.long {
	width: 100%;
}
.content.map {
	width: 100%;
	margin-top: 70px;
	margin-bottom: 30px;
	padding: 0px;
	border-top: 1px solid rgb(205, 205, 205);
	border-bottom: 1px solid rgb(205, 205, 205);
}
.content > .title {
	position: absolute;
	top: -16px;
	left: 0px;
	padding-right: 10px;
	background-color: white;
	font-size: 24px;
	font-weight: bolder;
}
.content.map > .legend {
	position: absolute;
	top: -50px;
	left: 10%;
	z-index: 5;
	padding: 20px 30px;
	background-color: rgb(225, 225, 225);
	border: 1px solid rgb(205, 205, 205);
	box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
}
.content.map > .legend .title {
	margin-bottom: 35px;
	font-size: 24px;
	font-weight: bolder;
}
.content.map > .legend .name {
	margin-bottom: 10px;
	font-size: 14px;
	font-weight: bold;
}
.content.map > .legend .position {
	margin-bottom: 20px;
	font-size: 14px;
}
.content.map > .legend .email {
	margin-bottom: 10px;
	font-size: 14px;
	text-decoration: underline;
	text-underline-offset: 2px;
}
.content.map > .legend .tel {
	font-size: 14px;
}
.content.map div.map {
	height: 500px;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
}
.content > .news_item {
	margin-top: 20px;
	margin-bottom: 10px;
	border-bottom: 1px solid rgb(225, 225, 225);
	cursor: pointer;
}
.content > .news_item:last-child {
	margin-bottom: 0px;
	padding-bottom: 0px;
	border-bottom: none;
}
.content > .news_item > .title {
	margin-bottom: 5px;
	font-weight: bolder;
}
.content > .news_item > .desc {
	margin-bottom: 5px;
	padding-left: 10px;
	padding-right: 10px;
}
.content > .news_item > .stamp {
	font-size: 12px;
	color: rgb(175, 175, 175);
	text-align: right;
}
.content.services-list > .serviceCategory > .subtitle {
	margin-top: 20px;
	margin-bottom: 10px;
	font-size: 20px;
	font-weight: bolder;
}
.content.services-list > .serviceCategory > .listitem {
	padding-left: 20px;
	line-height: 24px;
	cursor: pointer;
}
</style>
<style>
.content.company-desc > .context > div {
	text-indent: 35px;
	line-height: 25px;
}
.content.company-desc > .context > .title {
	margin-top: 30px;
	margin-bottom: 15px;
	font-size: 20px;
	font-weight: bolder;
	text-indent: 0px;
}
.content.company-desc > .context > .subtitle {
	margin-top: 20px;
	margin-bottom: 10px;
	font-weight: bolder;
}
</style>

<script>
import Slider from '@/components/slider.vue';
import FootMark from '@/components/footmark.vue';
import QRCode from '@/components/qrcode.vue';
import HomeMenu from '@/components/homemenu.vue';
import HomeWorkerWall from '@/components/homepicwall.vue';

const MaxHeight = 600;
const BDMapCBs = [];
var container = null;
var current = null;
var timerScroll = null;
var timerBDMap = null;
var BDMapLoaded = false;

window.initBDMap = function () {
	if (!current) return;
	current.onBDMapLoaded();
};

export default {
	name: 'Home',
	components: {Slider, HomeMenu, QRCode, HomeWorkerWall, FootMark},
	data () {
		return {
			slideDelay: 3000,
			companyDesc: '',
			companyName: '',
			companyPos: '',
			companyEmail: '',
			companyTel: '',
			news: [],
			services: [],
		}
	},
	mounted () {
		current = this;
		BDMapLoaded = false;

		this.loadBDMap();
		this.loadPageInfo();

		container = this.$refs.container;
		container.addEventListener('scroll', this.onScroll);
		container.addEventListener('mousewheel', this.onScroll);
		window.addEventListener('resize', this.onResize);
		this.onResize();
	},
	unmounted () {
		current = null;

		if (!!timerScroll) {
			clearTimeout(timerScroll);
			timerScroll = null;
		}
		container.removeEventListener('scroll', this.onScroll);
		container.removeEventListener('mousewheel', this.onScroll);
		window.removeEventListener('resize', this.onResize);
	},
	methods: {
		loadBDMap () {
			if (!!window.BMap) {
				BDMapLoaded = true;
				let tasks = BDMapCBs.splice(0, BDMapCBs.length);
				tasks.forEach(cb => cb());
				return;
			}

			var script = document.createElement('script');
			timerBDMap = setTimeout(() => {
				if (BDMapLoaded) return;
				BDMapLoaded = true;
				var tasks = BDMapCBs.splice(0, BDMapCBs.length);
				tasks.forEach(cb => cb());
				timerBDMap = null;
			}, 5000);
			script.onerror = () => {
				if (BDMapLoaded) return;
				if (!!timerBDMap) clearTimeout(timerBDMap);
				BDMapLoaded = true;
				var tasks = BDMapCBs.splice(0, BDMapCBs.length);
				tasks.forEach(cb => cb());
			};
			script.src = "http://api.map.baidu.com/api?v=3.0&ak=kzVWE17kVgP5pfaGLhagGeMSZ1CXcuu4&callback=initBDMap";
			document.body.appendChild(script);
		},
		waitForBDMap (cb) {
			if (!!window.BMap || BDMapLoaded) return cb();
			BDMapCBs.push(cb);
		},
		onBDMapLoaded () {
			if (BDMapLoaded) return;
			if (!!timerBDMap) clearTimeout(timerBDMap);
			BDMapLoaded = true;
			var tasks = BDMapCBs.splice(0, BDMapCBs.length);
			tasks.forEach(cb => cb());
		},
		onResize () {
			var rect = document.body.getBoundingClientRect();
			EventMetro.pub('window_resize', rect.width, rect.height, 60);
		},
		onScroll () {
			if (!!timerScroll) {
				clearTimeout(timerScroll);
			}
			timerScroll = setTimeout(this.doScroll, 10);
		},
		doScroll () {
			var rect = document.body.getBoundingClientRect();
			var height = rect.height;
			var top = this.$refs.container.scrollTop;
			var limit = height - 60;
			limit = limit > MaxHeight ? MaxHeight : limit;

			EventMetro.pub('home_scroll', top, height, top > limit);
		},
		showMap (x, y) {
			var hasMap = false;
			if (!!window.BMap) {
				try {
					var BDMap = new BMap.Map(this.$refs.baidumap);
					BDMap.centerAndZoom(new BMap.Point(x - 0.0010, y), 18);
					var marker = new BMap.Marker(new BMap.Point(x, y));
					BDMap.addOverlay(marker);
					hasMap = true;
				}
				catch {
					hasMap = false;
				}
			}
			if (!hasMap) {
				this.$refs.baidumap.style.backgroundImage = 'url(/pic/map.png)';
			}
		},
		async loadPageInfo () {
			var hash = this.$route.query.hash, visited = false;
			if (!!hash) {
				visited = sessionStorage.get('visit-home-' + hash) || false;
			}
			else {
				visited = true;
				hash = '';
			}

			var info = await BackEnd.getHomeInfo(hash, visited);
			if (!info) {
				return;
			}
			if (!!hash) {
				sessionStorage.set('visit-home-' + hash, true);
			}

			var slides = [], workers = [];
			info.forEach(item => {
				if (item.type === 'slidedelay') {
					this.slideDelay = item.value;
					return;
				}
				else if (item.type === 'company') {
					this.$refs.companyDesc.innerHTML = item.desc;
					return;
				}
				else if (item.type === "communication") {
					this.companyName = item.name;
					this.companyPos = item.position;
					this.companyEmail = item.email;
					this.companyTel = item.phone;
					this.waitForBDMap(() => {
						this.showMap(...item.location);
					});
					return;
				}
				else if (item.type === "worker") {
					workers.push(item);
					return;
				}
				else if (item.type === "slidepic") {
					slides.push(item);
					return;
				}
				else if (item.type === 'news') {
					this.news.push(...item.news.map(news => {
						news.stamp = stamp2string(news.stamp);
						return news;
					}));
					return;
				}
				else if (item.type === 'services') {
					let services = {};
					item.services.forEach(item => {
						var list = services[item.type];
						if (!list) {
							list = [];
							services[item.type] = list;
						}
						list.push(item);
					});
					for (let name in services) {
						this.services.push({
							name, list: services[name]
						});
					}
				}
			});

			EventMetro.pub('update_slider', slides);
			EventMetro.pub('update_worker_wall', workers);
		},
		goNews (type, id) {
			this.$router.push({path: '/news/' + type + '/' + id});
		},
		goService (id) {
			var hash = this.$route.query.hash;
			this.$router.push({path: '/services', query: {hash, item: id}});
		},
	}
}
</script>