<template>
	<div ref="container" class="slider_container">
		<div class="slider_item" v-for="(item, i) in slides" :style="{backgroundImage:'url('+item.pic+')',left:((i-index)*100)+'%'}">
			<div class="slider_caption" v-if="!!item.desc">{{item.desc}}</div>
		</div>
		<div class="button left" :invalid="index===0" @click="slidePrev"><i class="fa fas fa-angle-left"></i></div>
		<div class="button right" :invalid="(slides.length===0)||(index===slides.length-1)" @click="slideNext"><i class="fa fas fa-angle-right"></i></div>
		<div class="slider_hints" v-if="slides.length>1">
			<div v-for="(hint, i) in slides" class="slider_hint" :current="index===i"></div>
		</div>
	</div>
</template>

<style scoped>
.slider_container {
	position: relative;
	width: 100%;
	height: 0px;
	padding-top: 40%;
	overflow: hidden;
}
.slider_item {
	position: absolute;
	top: 0px;
	left: 0px;
	width: 100%;
	height: 100%;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	transition: left 300ms ease-in-out;
}
.slider_caption {
	position: absolute;
	bottom: 15px;
	left: 10px;
	right: 10px;
	padding: 10px;
	background-color: rgba(36, 36, 36, 0.6);
	box-shadow: inset 0px 0px 5px black;
	text-align: center;
	font-size: 14px;
	color: rgb(205, 205, 205);
	text-shadow: 1px 1px 3px black;
}
.button {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	padding: 10px;
	background-color: rgba(36, 36, 36, 0.3);
	border-radius: 5px;
	font-size: 50px;
	font-weight: bolder;
	color: rgb(205, 205, 205);
	text-shadow: 1px 1px 3px black;
	cursor: pointer;
	transition: all 300ms ease-in-out;
}
.button:hover {
	color: rgb(245, 245, 245);
	background-color: rgba(36, 36, 36, 0.6);
}
.button[invalid="true"] {
	color: rgba(255, 255, 255, 0);
	text-shadow: 1px 1px 3px transparent;
	background-color: rgba(36, 36, 36, 0);
	pointer-events: none;
}
.button.left {
	left: 20px;
}
.button.right {
	right: 20px;
}
.slider_hints {
	position: absolute;
	bottom: 1px;
	left: 30px;
	right: 30px;
	text-align: center;
}
.slider_hint {
	display: inline-block;
	width: 8px;
	height: 8px;
	margin-left: 10px;
	margin-right: 10px;
	border-radius: 4px;
	background-color: rgb(205, 205, 205);
	border: 1px solid rgba(36, 36, 36, 0.5);
	box-shadow: 1px 1px 2px rgba(25, 25, 25, 0.6);
	transition: all 300ms ease-in-out;
}
.slider_hint[current="true"] {
	width: 20px;
	margin-left: 5px;
	margin-right: 5px;
	background-color: rgb(245, 245, 245);
}
@media screen and (max-width: 600px) {
	.button {
		padding: 5px;
		font-size: 30px;
		font-weight: bolder;
	}
}
</style>

<script>
const MaxHeight = 600;
var current, timer;
EventMetro.sub('update_slider', (slides) => {
	if (!current) return;
	current.init(slides);
});
EventMetro.sub('window_resize', (width, height, offset) => {
	if (!current) return;
	current.onResize(width, height, offset);
});

export default {
	name: "Slider",
	data () {
		return {
			slides: [],
			index: 0,
		}
	},
	props: {
		delay: Number
	},
	mounted () {
		current = this;
	},
	unmounted () {
		current = null;
		if (!!timer) clearTimeout(timer);
	},
	methods: {
		init (slides) {
			this.index = 0;
			slides.forEach(item => {
				this.slides.push({
					desc: item.desc || '',
					pic: item.pic,
				});
			});
			if (slides.length > 1) {
				timer = setTimeout(current.autoSlideNext, current.delay);
			}
		},
		slidePrev () {
			if (this.index < 0) return;
			this.index --;
			if (!!timer) {
				clearTimeout(timer);
				timer = setTimeout(this.autoSlideNext, this.delay);
			}
		},
		slideNext () {
			if (this.index > this.slides.length - 1) return;
			this.index ++;
			if (!!timer) {
				clearTimeout(timer);
				timer = setTimeout(this.autoSlideNext, this.delay);
			}
		},
		autoSlideNext () {
			if (this.index < this.slides.length - 1) this.index ++;
			else this.index = 0;
			timer = setTimeout(this.autoSlideNext, this.delay);
		},
		onResize (width, height, offset=0) {
			var limit = height - offset;
			var h = limit > MaxHeight ? MaxHeight : limit;
			this.$refs.container.style.height = h + 'px';
			this.$refs.container.style.paddingTop = '0px';
		}
	}
}
</script>