<template>
	<div class="qrframe" :show="showQR" @click="showQR=false">
		<div ref="qrcode" class="qrcode"></div>
	</div>
</template>

<style scoped>
.qrframe {
	position: fixed;
	top: 0%;
	left: 0%;
	width: 100%;
	height: 0%;
	background-color: rgba(75, 75, 75, 0);
	opacity: 0;
	transition: opacity 300ms ease-in-out, height 300ms step-end;
	pointer-events: none;
}
.qrframe[show="true"] {
	height: 100%;
	background-color: rgba(36, 36, 36, 0.2);
	opacity: 1;
	transition: opacity 300ms ease-in-out, height 300ms step-start;
	pointer-events: auto;
}
.qrframe .qrcode {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 270px;
	height: 270px;
	padding: 10px;
	background-color: white;
	border-radius: 5px;
	border: 1px solid rgba(0, 0, 0, 0.3);
	transform: translate(-50%, -50%);
}
</style>

<script>
var current;
EventMetro.sub('promote', () => {
	if (!current) return;
	current.showPromote();
});

export default {
	name: "QRCode",
	data () {
		return {
			showQR: false,
		}
	},
	mounted () {
		current = this;
	},
	unmounted () {
		current = null;
	},
	methods: {
		async showPromote () {
			var session = localStorage.get('sessionID');
			if (!session) {
				EventMetro.pub('show_login');
				return;
			}

			var {hash} = this.$route.query || undefined;
			var promoteID = await BackEnd.newPromote(session, hash);
			if (!promoteID) {
				return;
			}
			var url = location.origin + '/promote/' + promoteID;

			this.$refs.qrcode.innerHTML = '';
			var qrcode = new QRCode(this.$refs.qrcode, {
				text: url,
				width: 250,
				height: 250,
				colorDark : "#000000",
				colorLight : "#ffffff",
				correctLevel : QRCode.CorrectLevel.H
			});
			var qr = this.$refs.qrcode.querySelector('img');
			qr.style.marginLeft = 'auto';
			qr.style.marginRight = 'auto';
			qr.style.maxWidth = '100%';

			this.showQR = true;
		},
	}
}
</script>